* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url('https://super.abril.com.br/wp-content/uploads/2018/07/55de0cc80e21632c150004bfthinkstockphotos-4691080331.jpeg');
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  text-align: center;
}

header {
  border: 5px solid white;
  border-radius: 5px;
  margin: 20px auto;
  padding: 20px;
  width: 50%;
}

.solar-system h2 {
  font-size: 50px;
  margin: 40px auto;
  text-decoration: underline;
  width: 50%;
}

.eachPlanet {
  display: inline-block;
}

.eachPlanet img {
  width: 150px;
}

.missions h2 {
  font-size: 40px;
  margin: 40px auto;
  text-decoration: underline;
  width: 50%;
}

.mission-card {
  background: white;
  border: 1px solid white;
  border-radius: 5px;
  color: black;
  display: inline-block;
  margin: 5px 20px;
  padding: 5px;
}

.App {
  text-align: center;
}
